<template>
  <div>
    <h1>登录</h1>
    <!-- 角色 -->
    <div>
      角色
      <select v-model="user.role">
        <option value="1">外卖</option>
        <option value="0">电影</option>
      </select>
    </div>
    <div>账号: <input type="text" v-model="user.userName" /></div>
    <div>
      密码:
      <input type="text" v-model="user.pass" />
    </div>
    <div>
      <button @click="login">登录</button>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      user: {
        userName: "color_run",
        pass: "1995",
        role: 1,
      },
    };
  },
  methods: {
    login() {
      if (
        this.user.userName == "color_run" &&
        this.user.pass == "1995" &&
        this.user.role != ""
      ) {
        // 登录成功设置本地存储
        localStorage.setItem("isLogin", 1);
        // 角色设置存储
        localStorage.setItem("isRole", this.user.role);
        // 路由跳转到index
        this.$router.replace("/index");
      } else {
        alert("请输入正确账号和密码");
      }
    },
  },
};
</script>

<style>
</style>


